body {
  background: #F2F1EF;
  font-family: 'Roboto Regular' !important;
  margin: 0;
}
.content {
  width: 90%;
  margin: 3% auto;
}
.items,
.layer {
  display: inline-block;
  background: #fff;
  width: 70%;
  height: auto;
  padding: 20px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
}
.items h1,
.layer h1 {
  color: #BFBFBF;
  padding: 15px;
}
.layer {
  width: 100%;
  display: block;
  margin: 30px 0;
}
.filter {
  display: inline-block;
  background: #fff;
  float: right;
  width: 20%;
  height: auto;
  padding: 20px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  position: fixed;
  margin: 0 3%;
}
.filter .filter-block {
  width: 100%;
  margin-bottom: 7px;
}
.filter .filter-block input[type=text],
.filter .filter-block select {
  width: 100%;
}
.filter span.clear {
  float: right;
  font-size: 15px;
  margin-top: 10px;
  color: #7f8c8d;
  cursor: pointer;
}
.filter span.clear:hover {
  color: #5D8CAE;
}
.details {
  width: 100%;
  height: auto;
  border-top: 1px solid #BFBFBF;
  border-bottom: 1px solid #BFBFBF;
}
.details-row {
  width: 100%;
  height: auto;
}
.details-column {
  display: inline-block;
  height: auto;
}
.pager-wrap {
  width: 100%;
}
.pager-wrap .pager {
  text-align: center;
  margin: 20px 0 0px;
}
.pager-wrap .pager button {
  color: #6C7A89;
  background: #EDEEF0;
  width: 40px;
}
.pager-wrap .pager button:hover,
.pager-wrap .pager button.current {
  color: #fff;
  background: #5D8CAE;
}
.pager-wrap .pager span {
  padding: 10px;
  font-weight: bold;
}
span.radio {
  color: #7f8c8d;
