  -moz-osx-font-smoothing: grayscale;
  font-size: 19px;
}
.text-center {
  text-align: center;
}
.details-row.result-details {
  margin: -1px 0 1% 0;
}
.details-column {
  margin: 1%;
}
.row {
  width: 100%;
  height: 150px;
}
.row:after {
  clear: both;
}
.row .discipline {
  width: 21%;
  display: inline-block;
  float: left;
  margin: 2% 2%;
}
.row .discipline-head {
  height: 40px;
  text-align: center;
  background: #5D8CAE;
  color: #fff;
  line-height: 40px;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
}
.row .discipline-body {
  height: 110px;
  background: #EDEEF0;
  color: #5D8CAE;
  line-height: 110px;
  font-size: 40px;
  text-align: center;
}
.tests {
  margin-top: 20px;
}
.tests .test {
  background: #EDEEF0;
  color: #6C7A89;
  cursor: pointer;
  padding: 10px 20px;
  margin-bottom: 1px;
  font-size: 16px;
}
.tests .test:hover {
  color: #5D8CAE;
}
.tests .test:hover span.start {
  display: inline;
}
.tests .test:hover span.attempts {
  display: none;
}
.tests .test span.attempts,
.tests .test span.start {
  float: right;
}
.tests .test span.start {
  font-size: 20px;
  cursor: pointer;
  display: none;
}
.result {
  margin: 1% 0;
  width: 100%;
  border: 1px solid #BFBFBF;
  cursor: pointer;
}
.result:hover {
  border: 1px solid #5D8CAE;
}
.result.current {
  border: 1px solid #5D8CAE;
  background: #5D8CAE;
  color: #fff;
}
.result.current label,
.result.current span {
  color: #fff;
}
.item {
  background: #EDEEF0;
  color: #6C7A89;
  cursor: pointer;
  padding: 10px 20px;
  margin-bottom: 1px;
  font-size: 16px;
}
.item:hover {
  background: #5D8CAE;
  color: #fff;
}
.item:hover span {
  color: #fff;
}
